import { Component, ViewChild, ElementRef } from '@angular/core';
import { UserService } from '../../providers/user-service';
import { SettingsService } from '../../providers/settings-service';
import { IonicPage, Platform, LoadingController } from 'ionic-angular';


declare var google: any;

@IonicPage()
@Component({
	selector: 'page-map',
	templateUrl: 'map.html',
	providers: [SettingsService]
})
export class MapPage {
	path: string;
	loading: any;
	@ViewChild('mapCanvas') mapElement: ElementRef;
	constructor(public platform: Platform, public user: UserService, public settingsService: SettingsService, public loadingCtrl: LoadingController) {
	}

	ionViewDidLoad() {
		this.loading = this.loadingCtrl.create();
		this.loading.present().then(()=>{
			this.settingsService.getPath()
			.then(data => {
				this.path = data;
				let polygon = null;
				if(this.path) {
					let decodedPath = google.maps.geometry.encoding.decodePath(this.path);
					polygon = new google.maps.Polygon({
						paths: decodedPath,
						strokeOpacity: 0.8,
						strokeWeight: 0.1,
						fillOpacity: 0.35
					});
				}

				// this.confData.getMap().subscribe((mapData: any) => {
					let mapEle = this.mapElement.nativeElement;
					let map = new google.maps.Map(mapEle, {
						center: {
							"name": "Frango Sobre Rodas",
							"lat": 41.1294944,
							"lng": -8.6198778
						},
						zoom: 11
					});

					// mapData.forEach((markerData: any) => {
					// 	let infoWindow = new google.maps.InfoWindow({
					// 		content: `<h5>${markerData.name}</h5>`
					// 	});

					// // 	let marker = new google.maps.Marker({
					// // 		position: markerData,
					// // 		map: map,
					// // 		title: markerData.name
					// // 	});

					// 	marker.addListener('click', () => {
					// 		infoWindow.open(map, marker);
					// 	});
					// });

					google.maps.event.addListenerOnce(map, 'idle', () => {
						mapEle.classList.add('show-map');
					});

					if(polygon) {
						polygon.setMap(map);
					}
					this.loading.dismiss();

				// });
			}, error => {
				// console.log(error);
				this.loading.dismiss();
			});
		});
	}

	openSite(site: string) {
		window.open(site, '_blank');
	}
}



